#root, #root > [data-reactroot], html, body {
  height: 100%;
  margin: 0;
  overflow: hidden; }

#root, #root > [data-reactroot] {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column; }

.Resizer {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #eee;
  opacity: .9;
  z-index: 10;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center; }

.Resizer:focus {
  outline: none;
  background-color: #888; }

.Resizer.horizontal {
  height: 5px;
  margin: -5px 0;
  cursor: row-resize;
  width: 100%; }

.Resizer.vertical {
  width: 5px;
  margin: 0 -5px;
  cursor: col-resize; }

.ResizerHandle.vertical {
  background-color: #444;
  width: 3px;
  height: 16px; }



