.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  position: fixed;
  width: 100%; }

.clearfix:after {
  content: "";
  display: table;
  clear: both; }



